// https://www.gatsbyjs.com/docs/tutorial/getting-started/part-2/
// Option 2

// First step : Import Libraries -> React
import * as React from 'react'

// Rule 1 : component must return a single React element.
/* Rule 2 : but you can put as many elements inside that
top-level element as you want. The code snippet below
shows an example of a valid component and an invalid component: */

const ValidComponent = () => {
    return (
        <div>
            <h1>올바른 컴포넌트 구성 예시</h1>
            <p> 원칙 : 1 컴포넌트 1요소(element) 이지만, </p>
            <p>만약 div와 같이 상위 요소를 컴포넌트와 연결할 경우</p>
            <p>그 상위 요소 안에는 여러 요소를 넣을 수 있다.</p>
            <p>한 컴포넌트에 동등한 랭크의 요소를 여러개 넣으면 어떻게 될까?</p>
            <p>Shell에서 Parsing Error를 확인할 수 있다.</p>
        </div>
    )
}

